export default {
  data() {
    return {
     
    };
  },
  computed: {
   src(){
     return this.$route.query.ecgImage;
   }
  },
  mounted() {
    // this.getData()
   
  },
  methods: {
    getData() {
       this.$vux.loading.show({
            text: "加载中"
          });
      this.$ajax({
        data: {
          header: {
            action: "GetEEGData"
          },
        body: {
							eegid:this.$route.query.id
						}
        },
        success: res => {
          var data=res.body.map((t)=>{
            return t.EcgV/349
          });
          
          var ecgObj=new Ecg({
            datas:data
          });
          ecgObj.drawecg();
          ecgObj.ecg();
        }
      });
    }
  }
};



